import React , {useState}from 'react'
import Pizzas from './Pizzas'
import Loading from './Loading'

function Content({items , pizzas , snacks , desserts , drinks , combos , loading}) {
    const [activeItem , setActiveItem] = useState('–ü–∏—Ü—Ü—ã');
    function toggleActiveItem(index){
        setActiveItem(index)
    }
    const sizes = ['–º–∞–ª–µ–Ω—å–∫–∏–π' , '—Å—Ä–µ–¥–Ω–∏–π' , '–±–æ–ª—å—à–æ–π'],
          types = ['—Ç–æ–Ω–∫–æ–µ' , '—Ç—Ä–∞–¥–∏—Ü–∏–æ–Ω–Ω–æ–µ'];
    return (
        <div className="content">
          <div className="container">
            <div className="content__top">
              <div className="categories">
                <ul>
                  {
                      items.map((item , index) => (
                          <li
                          key={`item_${index}`}
                          onClick={() => toggleActiveItem(item)}
                          className={ activeItem === item ? 'active' : ''}
                          >{item}</li>
                      ))
                  }
                </ul>
              </div>
            </div>
            <h2 className="content__title">–í—Å–µ {activeItem.toLowerCase()}</h2>
            <div className={activeItem === '–ü–∏—Ü—Ü—ã' ? 'content__items active-content' : 'content__items'}>
                {
                    loading ?
                    pizzas.map(items => <Pizzas pizza={items} sizes={sizes} types={types} /> )
                    :
                    pizzas.map(items => <Loading items={items}/> )
                    
                }
            </div>
            <div  className={activeItem === '–ö–æ–º–±–æ' ? 'content__items active-content' : 'content__items'}>
              {
                combos.map(items => <Pizzas pizza={items} types={types} sizes={sizes}/>)
              }
            </div>
            <div  className={activeItem === '–ó–∞–∫—É—Å–∫–∏' ? 'content__items active-content' : 'content__items'}>
              {
                snacks.map(items => <Pizzas pizza={items} sizes={sizes} types={types} />)
              }
            </div>
            <div  className={activeItem === '–î–µ—Å–µ—Ä—Ç—ã' ? 'content__items active-content' : 'content__items'}>
              {
                desserts.map(items => <Pizzas pizza={items} sizes={sizes} types={types} />)
              }
            </div>
            <div  className={activeItem === '–ù–∞–ø–∏—Ç–∫–∏' ? 'content__items active-content' : 'content__items'}>
              {
                drinks.map(items => <Pizzas pizza={items} types={types} sizes={sizes}/>)
              }
            </div>
            
            </div>
        </div>
    )
}

export default Content
