import React , { useState }from 'react'
import Store from '../store/store';

function Pizzas({pizza , sizes , types }) {
    const [activeSize , setActiveSize]= useState(pizza.sizes[0]);
    const [activeType , setActiveType]= useState(pizza.types[0]);
    function toggleActiveSize(index){
        setActiveSize(index)
    }
    function toggleActiveType(index){
        setActiveType(index)
    }
    return ( 
              <div className="pizza-block">
                  <img
                    className="pizza-block__image"
                    src={pizza.imageUrl}
                    alt="Pizza"
                  />
                  <h4 className="pizza-block__title">{pizza.name}</h4>
                  <div className="pizza-block__selector">
                    <ul>
                      {
                          types && types.map((items , index) => (
                              <li
                              key={`size_${index}`}
                              onClick={() => toggleActiveType(index)}
                              className={activeType === index ? 'active': !pizza.types.includes(index) ? 'disabled'  : ''}
                              >{items} </li>
                          ))
                      }
                    </ul>
                    <ul>
                      {
                          sizes && sizes.map((size , index) => (
                              <li
                              key={`size_${index}`}
                              onClick={() => toggleActiveSize(index)}
                              className={activeSize === index ? 'active': !pizza.sizes.includes(index) ? 'disabled'  : ''}
                              >{size}</li>
                          ))
                      }
                    </ul>
                  </div>
                  <div className="pizza-block__bottom">
                    <div className="pizza-block__price">–æ—Ç {pizza.price} —Å–æ–º</div>
                    <div className="button button--outline button--add" onClick={() => Store.addElem(pizza)}>
                      <svg
                        width="12"
                        height="12"
                        viewBox="0 0 12 12"
                        fill="none"
                        xmlns="http://www.w3.org/2000/svg"
                      >
                        <path
                          d="M10.8 4.8H7.2V1.2C7.2 0.5373 6.6627 0 6 0C5.3373 0 4.8 0.5373 4.8 1.2V4.8H1.2C0.5373 4.8 0 5.3373 0 6C0 6.6627 0.5373 7.2 1.2 7.2H4.8V10.8C4.8 11.4627 5.3373 12 6 12C6.6627 12 7.2 11.4627 7.2 10.8V7.2H10.8C11.4627 7.2 12 6.6627 12 6C12 5.3373 11.4627 4.8 10.8 4.8Z"
                          fill="white"
                        />
                      </svg>
                      <span > –î–æ–±–∞–≤–∏—Ç—å</span>
                    </div>
                    </div>
              </div>
    )
}

export default Pizzas
